export const piano =
  '<h1>Pianoles</h1>\n' +
  '    <p>De piano is een fantastisch instrument. Je combineert een melodie, meestal gespeeld met de rechterhand, met een begeleiding waar vaak een bas -en akkoordpartij wordt gecombineerd. Kortom: met een piano kun je ‘een compleet stukje muziek wegzetten’. Niet voor niets dat een piano sterk is vertegenwoordigd in diverse muziekstijlen: klassiek, jazz en popmuziek.</p>\n' +
  '    <p>Wanneer je thuis een piano of een elektrische piano hebt dan kun je hiervoor bij me les nemen. Meld je aan voor een eerste kennismakingsles dan bekijken we samen welke richting je op wilt gaan:</p>\n' +
  '    <ul>\n' +
  '        <li>Wil je vooral van bladmuziek spelen?</li>\n' +
  '        <li>Wil je zelf de mooiste liedjes uitproberen en uit gaan zoeken?</li>\n' +
  '        <li>Wil je jezelf of anderen met akkoorden begeleiden bij samenzang?</li>\n' +
  '        <li>Kies je voor één bepaalde richting of wil je graag muziekstijlen combineren?</li>\n' +
  '    </ul>\n' +
  '    <p>Het plezier beleven aan het piano spelen staat voorop. Als docent vind ik het wel belangrijk dat theorie en praktijk “hand in hand’ moeten gaan bij de muziekles. Je kunt op youtube de mooiste melodieën naspelen via tutorials. Daar is niets mis mee. Je bereikt in korte tijd een mooi resultaat. Maar ik vind het wel belangrijk dat je dan ook moet weten wat je speelt! Ik leg je spelenderwijs uit dat muziek is opgebouwd uit akkoorden; dat er een regelmaat in te ontdekken valt; dat je moet weten hoe je met akkoorden en een baslijn een stuk muziek fraaier kunt maken. Wanneer je kennis hebt van deze bouwstenen en je dit spelenderwijs op de toetsen hebt ervaren dan ben je na verloop van tijd zelf in staat om zonder tutorials je eigen deuntjes te spelen.</p>\n' +
  '    <p>Ik hekel de lespraktijk die ik zelf heb ondervonden als jonge leerling: het notenlezen stond centraal; alsof je een typemachine bespeelt. Je gehoor schakel je uit want je let vooral of iedere noot goed past bij de toets die je moet aanslaan. Omdat ik lange tijd in een allround band als toetsenist/zanger/saxofonist heb meegespeeld werd ik me bewust van het belang van het spelen van akkoorden. Daarom probeer ik in de pianoles vooral ook jouw muzikaal gehoor zo te ontwikkelen dat je ontdekt wat de bouwstenen zijn van een muziekstuk. Je kunt hier dan later op een creatieve manier mee aan de slag gaan!</p>';
