export const orgelles =
  '<h1>Orgelles</h1>\n' +
  '<p>Het orgel is één van de oudste toetsinstrumenten die er is. In de tijd zonder elektriciteit werd het (kerk) orgel ‘aangeblazen’ door een handmatig bediende blaasbalg. In de kerk speelt het orgel een belangrijke rol bij de liedbegeleiding.</p>\n' +
  '<p>Als beginnende leerling heb ik les gehad op een electronisch huisorgel. Dat was in die tijd in de mode. Een instrument met 2 klavieren en een beperkt voetpedaal. Toen mijn docent destijds stopte op de muziekschool waar ik les had nam hij mij mee naar een andere leslocatie: het grote kerkorgel. Een imposant instrument met 2 grote klavieren en een uitgebreid voetpedaal. De ‘wereld van het klassieke orgel’ kwam bij mij naar binnen. Een schitterende klank in een kerk met een behoorlijke akoestiek. Ik maakte kennis met de muziek van Buxtehude en Bach o.a. Maar ook het subtiele repertoire van Engelse grootmeesters: voluntaries bijvoorbeeld gespeeld zonder voetpedaal. Als kerkorganist heb ik heel wat eucharistievieringen en rouw -en trouwdiensten mogen begeleiden. Binnen deze ‘cultuur’ is de improvisatie ook een belangrijk aspect.</p>\n' +
  '<p>Mocht je belangstelling hebben voor dit instrument dan kun je bij mij ook spelen op een huisorgel met groot voetpedaal. Het plezier van het musiceren staat uiteraard voorop. Ik kan je op weg helpen met het begeleiden van liederen op het orgel. Wederom gaat theorie en praktijk hand in hand. Misschien kan ik je wel zo op weg helpen dat je zelf gaat improviseren op de manualen en het voetpedaal!</p>';
